body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #eee;
}

.Area-Row{
  background-color: #eee;
  margin-bottom: 60px;
}

.Area-Row:after {
  content: "";
  display: table;
  clear: both;
}

.No-Result-Found{  
  font-family: 'Lato', 'Helvetica', 'Segoe UI', 'Tahoma', 'Arial', 'sans-serif';
  text-align: center;
  font-size: 2.25em;
  font-style: italic;
  font-weight: 300;
  color: #757575;
  padding-top: 30vh;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.App-Header {
  background-color: #282c34;
  padding: 10px;
  text-align: center;
  font-weight: bold;
  font-size: x-large;
  color: #f1f1f1;
  }
.App-Search input {
    float: left;
    display: block;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    width: 100%;
    font-size: 24px;
    border: none;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16), 0 0 0 1px rgba(0,0,0,0.08);
  }
  
  .App-Search input:focus, .App-Search input:hover{
    box-shadow: 0 3px 8px 0 rgba(0,0,0,0.2), 0 0 0 1px rgba(0,0,0,0.08);
    outline: 0;
}
.Movie-Container {
    float: left;
    width: 33.333%;
    height: 300px;
  }

.Movie-Item{
  padding: 10px;
  height: 100%;
  display: flex;
}

.Banner{    
  background-color: white;
  text-align: center;
  width: 200px;
  height: 100%;
  border: none;
  border-radius: 5px 0 0 5px;
  box-shadow: 0 1px 3px 0 rgba(60, 64, 67, 0.3), 0 4px 8px 3px rgba(60, 64, 67, 0.15);
}

.Banner > img{
  height: 100%;
  width: 100%;
  border-radius: 5px 0 0 5px;
}

.Details{
  width: calc(100% - 200px);
  border: none;
  background-color: #fff;
  padding: 5px;
  border-radius: 0 5px 5px 0;
  box-shadow: 0 1px 3px 0 rgba(60, 64, 67, 0.3), 0 4px 8px 3px rgba(60, 64, 67, 0.15);
}

.Details-Container > .Name{
  font-size: 1.5em;
  border-bottom: 1px solid #ccc;
  padding: 2px;
  white-space: nowrap; 
  overflow: hidden;
  text-overflow: ellipsis;
}

.Details-Container > .Tabular{
  width: 100%;
  border-bottom: 1px solid #ccc;
}

.Details-Container > .Overview{
  width: 100%;
}

.Details-Header{
  width: 40%;
}

.Details-Value{
  font-weight: bold;
  padding: 5px;
}

.Details-Container{
  height: 90%;
  overflow: hidden;
}

.Link-Container{
  text-align: center;
}

.Link-Container > a{
  background-color: #eee;
  color: black;
  border: 1px solid #ccc;
  padding: 2px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}

.Rating-Checked{
  color: orange;
}


@media screen and (max-width: 1120px) {
  .Banner{
    width: 150px;
  }
  
  .Details{
    width: calc(100% - 150px);
  }

  .Movie-Container {
    height: 260px;
  }
}

@media screen and (max-width: 992px) {
  .Movie-Container {
    width: 50%;
    height: 250px;
  }
}

@media screen and (max-width: 692px) {
  .Movie-Container {
    width: 100%;
    height: 240px;
  }
}
.App-Footer {
    background-color: #eee;
    padding: 10px;
    position: fixed;
    text-align: center;
    bottom: 0;
    font-family: 'Lato', 'Helvetica', 'Segoe UI', 'Tahoma', 'Arial', 'sans-serif';
    font-size: 1.5em;
    width: 100%;
    font-weight: 500;
    color: #757575;
    border-top: 1px solid #000;
}

.App-Footer > .Results-Counter {
    float:left;
    width:250px;
}

.App-Footer > .Pagination-Group {
    float:right;
    width:250px;
}

.App-Footer > .Page-Number {
    margin:0 auto;
    width:250px;
}

.App-Footer > .Pagination-Group > span{
    cursor: pointer;
}


@media screen and (max-width: 600px) {
    .App-Footer > .Results-Counter {
        display: none;
    }
    
    .App-Footer > .Pagination-Group {
        float:none;
        width:auto;
    }
    
    .App-Footer > .Page-Number {
        float:none;
        width:auto;
    }
  }
